import React, { useEffect } from "react";
import { Link } from "react-router-dom";

export default function Item(props) {
  return (
    <tr className="hover-product even">
      <td className="sorting_1">2</td>
      <td >
      <Link
          to="/staff/edit/1?key=staff"
        >
          Nguyễn Văn A
        </Link>
      </td>
      <td>user</td>
      <td>0123456798</td>
      <td>8 (Năm)</td>
      <td>8/12 (Bài)</td>


      <td>Nhân viên</td>

      <td style={{ display: "flex" }}>
        <Link
          className="btn btn-primary btn-sm show"
          to="/staff/report/1?key=staff"
        >
          <i className="fas fa-fw fas fa-chart-bar " /> Báo cáo
        </Link>
        <button
          onClick={props.handleDelete
          }
          data-toggle="modal"
          data-target="#modalUID"
          className="btn btn-danger btn-sm show"
          style={{ marginLeft: "10px" }}
        >
          <i className="fa fa-trash" /> Xóa
        </button>
      </td>
    </tr>
  );
}
